.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
    
}
.subtotal__gift{
    display: flex;
    align-items: center;
}
.subtotal__gift > input{
    margin-right: 5px;
}
.subtotal >button{
    background-color: rgb(156, 253, 156);
    border-radius: 10px;
    padding: 2% 5%;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: black;
    color: black;
    cursor: pointer;
    transition: all .2s ease-in-out;
}
.subtotal>button:hover{
    background: rgb(72, 255, 0);
}